import React, { useState } from 'react'
import TagList from './tagList'
import {defaultIncomeTags} from '../store/iconList'
import styled from 'styled-components'
import LabelDatahelper from '../store/labelDatahelper'

const TabsWrap =styled.div`
  height:100%;
  // display:flex;
  // flex-direction:column;
  .tabBar{
    display:flex;
    justify-content:center;
    align-items:center;
    font-size:20px;
    padding:4px 4px 0 4px;
    background:#ffda47;
    >span{
      display:flex;
      justify-content:center;
      align-items:center;
      position: relative;
      margin: 0 15px;
      padding:24px 0 8px;
      &.selected::after{
        content:'';
        position:absolute;
        height:1px;
        background:black;
        left:-40%;
        bottom:0;
        width:180%
      }
    }
  }
  .tabBar-taglist{
    height:82%;
    overflow:auto;
  }
`
export default function Tabs(props) {
  const ldb=new LabelDatahelper('labelRecord')
  const expenseTagList=ldb.readData().flat().filter((item)=>item.choose===true)
  const [navBarType,setNavBarType]=useState(true)
  //å¦‚æœåœ¨è¿™è·å–æ”¯å‡ºå›¾æ ‡åˆ—è¡¨ï¼Œä¼šå¯¼è‡´useEffectè§¦å‘ä¸¤æ¬¡äº‹ä»¶ï¼Œæš‚æ—¶å…ˆåœ¨å­ç»„ä»¶è·å–å§
  const incomeTagList=defaultIncomeTags//è·å–æ”¶å…¥å›¾æ ‡åˆ—è¡¨
  function navbarHandle(e) {//è·å–moneyType
    let value = e==="-"?true:false
    setNavBarType(value)//é€‰æ‹©tagåˆ—è¡¨
    const defaultTag=value===true?expenseTagList[0]:incomeTagList[0] //åˆ‡æ¢tagåˆ—è¡¨æ—¶è®¾ç½®é»˜è®¤tag
    props.moneyType(e,defaultTag)
  }
  function handleTag(tag){//è·å–tag
    props.tagName(tag)
  }
  
  return (
    <TabsWrap>
      <div className="tabBar">
        <span onClick={()=>navbarHandle("-")} className={navBarType?"selected":""}>æ”¯å‡º</span>
        <span onClick={()=>navbarHandle("+")} className={!navBarType?"selected":""}>æ”¶å…¥</span>
      </div>
      <div className="tabBar-taglist">
        {navBarType? (
          <TagList handleTag={handleTag} ifPushAddTag/>
        ) : (
          <TagList tagList={incomeTagList} handleTag={handleTag}/>
        )}
      </div>
    </TabsWrap>
  )
}
